const apiKey = 'EP5EVNSVNtn7B26aR37CHvGGjZc2K1qgmRI4q1pJMpZkxqez6Dgea5n-tn2u7SWBoPu5K-CEuEXf562vhBK2kQF5nRFKq7QjyPNgfFVvm1g5vpmDlzX5EBYngcWpXnYx';
const Yelp = {
    search(term, location, sortBy) {
        return fetch(`https://cors-anywhere.herokuapp.com/https://api.yelp.com/v3/businesses/search?term=${term}&location=${location}&sort_by=${sortBy}`, {headers: {Authorization: `Bearer ${apiKey}`}
    }).then(response => {
        return response.json();
    }).then(jsonResponse => {
        if (jsonResponse.businesses) {
            return jsonResponse.businesses.map(business => {
                console.log(business);
                return {
                    id: business.id,
                    imageSrc: business.image_url,
                    name: business.name,
                    address: business.location.address1,
                    city: business.location.city,
                    state: business.location.state,
                    zipCode: business.location.zipcode,
                    category: business.categories[0].title,
                    rating: business.rating,
                    reviewCount: business.reviewCount,
                }
            });

        }
    });
    }
};
export default Yelp;